a {
  text-decoration: none;
  color: #E0ECFF;
}
.mining-claim {
  height: 152px;
  border-top: 1px solid #203B60;
}
.mining-claim .deri-num {
  color: #E0ECFF;
  font-weight: bold;
}
.mining-claim .cliam-button img {
  margin-right: 10px;
}
.mining-claim .deri-num-slider {
  font-weight: normal;
}
.mining-claim .deri-num-slider .deri-claim-time {
  font-size: 20px;
  line-height: 26px;
  color: #FFFFFF;
  display: flex;
  align-items: center;
  margin-bottom: 8px;
}
.mining-claim .deri-num-slider .deri-claim-time .text {
  border-bottom: 0;
  margin-left: 16px;
  margin-top: 8px;
}
.mining-claim .deri-num-slider .deri-claim-text {
  font-size: 14px;
  margin-bottom: 8px;
}
@media only screen and (max-width: 374px) {
  .mining-claim {
    margin: 0 auto;
    padding: 0 24px;
    padding-top: 28px;
    height: 248px;
  }
  .mining-claim .deri-num-slider .deri-claim-slider .time-slider {
    width: 327px;
  }
  .mining-claim .cliam-button {
    margin-top: 5px;
  }
  .mining-claim .deri-claim-text {
    margin-top: 5px;
  }
}
@media only screen and (min-width: 375px) and (max-width: 759px) {
  .mining-claim {
    margin: 0 auto;
    padding: 0 24px;
    padding-top: 28px;
    height: 248px;
  }
  .mining-claim .deri-num-slider .deri-claim-slider .time-slider {
    width: 327px;
  }
  .mining-claim .cliam-button {
    margin-top: 5px;
  }
  .mining-claim .deri-claim-text {
    margin-top: 5px;
  }
}
@media only screen and (min-width: 760px) and (max-width: 1139px) {
  .mining-claim .mining-claim-info {
    display: flex;
    margin: 0 auto;
    height: 152px;
    align-items: center;
    justify-content: space-between;
    padding: 0 24px;
  }
  .mining-claim .mining-claim-info .deri-num-slider {
    width: 100%;
  }
  .mining-claim .mining-claim-info .deri-num-slider .deri-claim-slider .time-slider {
    width: 100%;
  }
}
@media only screen and (min-width: 1140px) and (max-width: 1567px) {
  .mining-claim .mining-claim-info {
    display: flex;
    align-items: center;
    width: 1140px;
    height: 152px;
    justify-content: space-between;
    padding-left: 24px;
    padding-right: 24px;
    margin: 0 auto;
  }
}
@media only screen and (min-width: 1568px) {
  .mining-claim {
    height: 152px;
  }
  .mining-claim .mining-claim-info {
    display: flex;
    align-items: center;
    height: 152px;
    justify-content: space-between;
    padding-left: 48px;
    padding-right: 48px;
  }
}
